<template><section><template>
  <h2><a href="#modal">Modal </a></h2>
  <div class="introduce">
    <z-button @click="alert.show = true">Alert</z-button>
    <z-modal :show="alert.show" :content="'alert'" @close="alert.show = false">
    </z-modal>
  </div>
  <div class="introduce">
    <z-button @click="confirm.show = true">Confirm</z-button>
    <z-modal :show="confirm.show" :type="'confirm'" :content="'confirm'" @close="confirm.show = false"></z-modal>
  </div>
</template>
<pre><code class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">z-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"alert.show = true"</span>&gt;</span>Alert<span class="hljs-tag">&lt;/<span class="hljs-name">z-button</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">z-modal</span>
<span class="hljs-attr">:show</span>=<span class="hljs-string">"alert.show"</span>
<span class="hljs-attr">:content</span>=<span class="hljs-string">"'alert'"</span>
<span class="hljs-attr">@close</span>=<span class="hljs-string">"alert.show = false"</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">z-modal</span>&gt;</span>
</code></pre>
<h3>API</h3>
<template>
  <div class="introduce-block">
    <p><code class="z-code">@close="xxx.show=false"</code><code class="z-code">show</code></p>
    <z-table :ths="['','','','','']" :trs="[
            ['show','Boolean','','-',''],
            ['content','String','','-',''],
            ['type','String','','alert','(alert,confirm)'],
            ['title','String','','',''],
            ['sure','String','','',''],
            ['cancle','String','','',''],
            ['sureEvent','Function','','-',''],
            ['cancelEvent','Function','','-',''],
          ]">
    </z-table>
  </div>
</template>
<h3>EVENT</h3>
<template>
  <z-table :ths="['', '', '']" :trs="[
          ['close','','-']
        ]">
  </z-table>
</template>

</section></template>

<script>
export default {
  data() {
    return {
      alert: {
        show: false
      },
      confirm: {
        show: false
      }
    }
  }
}
</script>


